@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  color: #fff;


  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;



}

body {
  background-color: #070707 ;
}



.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;

  margin-bottom: 50px;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;

}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(-225deg, #ac32e4 0%,
    #7918f2 48%, #4801ff 100%); 
  color: #e2e2e2;
}




.coin-input::-webkit-input-placeholder {
  color: #e2e2e2;
}




.coin-input:-ms-input-placeholder {
  color: #e2e2e2;
}




.coin-input::placeholder {
  color: #e2e2e2;
}

.coin-container {
    display: flex;
    justify-content: center;
    transition: background-color 0.3s;
    border-radius: 3px;
}



.coin-container:hover{
    background-color: #202020;
    cursor: pointer;
}
@media (min-width: 800px){
    .coin-container{
        padding-left: 5px;
        padding-right: 5px;
    }
    
}

.coin-row {
    display: flex;
    flex-direction: row;
    justify-items: start;
    align-items: center;
    height:  80px;
    border-bottom: 1px solid #d7d7d7;
    max-width:  1020px;
}

.coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
    min-width: 300px;
}


.coin h1 {
    font-size: 16px;
    width: 150px;

}

.coin img {
    height: 30px;
    width: 30px;
    margin-right: 10px;
}

.coin-symbol {
    text-transform: uppercase;
}


.coin-data {
    display: flex;
    text-align: center;
    justify-content: space-between;
    width: 100%;
}

@media (max-width: 400px) {
    .coin-data {
        font-size: 14px;
        width: 170px;
    }
    .coin h1 {
        font-size: 14px;
    }
    
}

.coin-price {
    width: 110px;

}

.coin-volume {
    width: 200px;
}

.coin-marketcap {
    width: 250px;
    text-align: right;
}

.coin-percent {
    width: 80px;
}

@media only screen and (max-width: 1024px) {
    .coin-marketcap {
        display: none;
    }
    .coin-percent {
        text-align: right;
    }
}

@media only screen and (max-width: 720px) {
    .coin-volume {
        display: none;
    }
    .coin {
        padding-right: 10px;
        min-width: 200px;
    }
}

@media only screen and (max-width: 460px) {
    .coin {
        min-width: 175px;
    }
    .coin-symbol {
        display: none;
    }
    .coin-percent{
        width: 60px;
    }
}
@media only screen and (max-width: 400px) {
    .coin {
        padding-right: 0;
        min-width: 160px;
    }
    
}

@media only screen and (max-width: 370px) {
    .coin {
        padding-right: 0;
        width: 165px;
    }
    
}

.red {
    color: #f00606;
}

.green {
    color: #11d811;
}
.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
    color: white;
    z-index: 10;
  }
  
.modal-main {
  position:fixed;
  background: black;
  width: 80%;
  
  height: auto;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  border: 1px #ac32e4 solid;
  border-radius: 10px;
  width: 500px;
  min-width: 300px;
}

@media (max-width: 510px) {
  .modal-main {
    width: auto;
  }
  
}

.display-block {
  display: block;
}

.display-none {
  display: none;
}

.coin-modal-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 4rem;
  padding-top: 2rem;
}
@media (max-width: 510px) {
  .modal-main {
    width: auto;
  }
  .coin-modal-container {
    padding: 1.5rem auto 3rem auto;
  }
  
}

@media (max-width: 420px) {
  .coin-modal-container {
    padding: 1.5rem 1.2rem 3rem 1.2rem;
  }
  
}

.coin-image {
  margin-bottom: 1rem;
}


.coin-modal-container .coin-name {
  margin-bottom: 1rem;
  font-size: 25px;
}

.coin-modal-container .coin-current {
  font-size: 20px;
  margin-bottom: 1rem;
  text-align: center;
}

.center {
  text-align: center;
  width: auto;
  font-size: 20px;
  margin-bottom: 1rem;
}

.btn-div{ 
  display: flex;
  justify-content: flex-end;
  margin: 10px;
}

.close-button {
  background: #ac32e4;

  border: 1px #ac32e4 solid;
  font-weight: 400px;
  font-size: 16px;
  padding-right: 7px;
  padding-left: 7px;
  border-radius: 3px;
}
.coin-modal-symbol {
  text-transform: uppercase;
  font-size: 22px;
  margin-bottom: 1rem;
}

.scroll-to-top {
    position: fixed;
    bottom: 0;
    right: 0;
    z-index: 9;
    margin: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
}

.btn {
    
    color: white;
    font-size: 2rem;
    background-color: #ac32e4;
    height: 3rem;
    width: 3rem;
    text-align: center;
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color 0.5s ease-in-out;
}

.btn:hover {
    background-color: #4801ff;
}

@keyframes fadeIn {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }
  
  @keyframes fadeOut {
    0% {
      opacity: 1;
    }
    100% {
      opacity: 0;
    }
  }
