@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');


* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  color: #fff;


  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;



}

body {
  background-color: #070707 ;
}



.coin-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: #fff;

  margin-bottom: 50px;
}

.coin-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.coin-text {
  margin-bottom: 32px;
  text-align: center;

}

.coin-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(-225deg, #ac32e4 0%,
    #7918f2 48%, #4801ff 100%); 
  color: #e2e2e2;
}




.coin-input::placeholder {
  color: #e2e2e2;
}
